@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

.page__body {
  position: relative;
  min-width: 320px;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  color: #283645;
  background-color: #ffffff;
}

.container {
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .container {
    width: 620px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 940px;
  }
}

.main {
  flex-grow: 1;
}

.button {
  display: block;
  min-width: 100px;
  padding: 21px;
  font-size: 18px;
  line-height: 1;
  font-weight: 700;
  border-width: 3px;
  border-style: solid;
  box-sizing: border-box;
  border-radius: 30px;
}
.button--primary {
  text-decoration: none;
  text-align: center;
  width: 100%;
  max-width: 300px;
  border-color: #ffffff;
  color: #ffffff;
}
.button--primary:hover {
  background-color: #ffffff;
  color: #d22856;
}
.button--primary:active {
  color: rgba(210, 40, 86, 0.3);
}
.button--secondary {
  font-family: inherit;
  appearance: none;
  cursor: pointer;
  max-width: 280px;
  color: #d22856;
  border-color: #d22856;
}
.button--secondary:hover {
  background-color: #d22856;
  color: #ffffff;
}
.button--secondary:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--tertiary {
  font-family: inherit;
  appearance: none;
  cursor: pointer;
  max-width: 280px;
  background-color: transparent;
  color: rgba(40, 54, 69, 0.1);
  border-color: rgba(40, 54, 69, 0.1);
}
.button--tertiary:hover {
  color: rgba(40, 54, 69, 0.3);
  border-color: rgba(40, 54, 69, 0.3);
}
.button--tertiary:active {
  background-color: rgba(40, 54, 69, 0.3);
  border-color: rgba(40, 54, 69, 0.3);
  color: rgba(255, 255, 255, 0.3);
}

.main-header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 21px 0;
  background-color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 660px) {
  .main-header {
    padding-top: 27px;
    padding-bottom: 43px;
  }
}
.main-header--active, .main-header--no-js {
  background-color: #283645;
  box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
@media (min-width: 1200px) {
  .main-header--active, .main-header--no-js {
    background-color: transparent;
    box-shadow: none;
  }
}

.main-header--active .main-header__button-open,
.main-header--no-js .main-header__button-open {
  display: none;
}

.main-header--active .main-header__button-close {
  display: block;
}
@media (min-width: 1200px) {
  .main-header--active .main-header__button-close {
    display: none;
  }
}

.main-header--no-js .main-header__button-close {
  display: none;
}

.main-header--active .main-header__menu,
.main-header--no-js .main-header__menu {
  display: block;
}
@media (min-width: 1200px) {
  .main-header--active .main-header__menu,
.main-header--no-js .main-header__menu {
    display: flex;
  }
}

.main-header--active .main-menu__link,
.main-header--no-js .main-menu__link {
  background-color: #283645;
  box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
@media (min-width: 1200px) {
  .main-header--active .main-menu__link,
.main-header--no-js .main-menu__link {
    background-color: transparent;
    box-shadow: none;
  }
}

.main-header__container {
  display: flex;
  align-items: center;
}

.main-header__navigation {
  margin-right: auto;
}
@media (min-width: 1200px) {
  .main-header__navigation {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin-right: 0;
  }
}

.logo {
  display: flex;
  align-items: end;
}
.logo:hover .logo__img, .logo:hover .logo__img-header, .logo:hover .logo__text, .logo:hover .logo__text-stars {
  fill: #d22856;
}
.logo:active .logo__img, .logo:active .logo__img-header, .logo:active .logo__text, .logo:active .logo__text-stars {
  fill: rgba(255, 255, 255, 0.3);
}

.logo__img {
  display: none;
  margin-right: 11px;
  fill: #ffffff;
}
@media (min-width: 660px) {
  .logo__img--tablet {
    display: inline-block;
  }
}
@media (min-width: 1200px) {
  .logo__img--tablet {
    display: none;
  }
}

.logo--theme-color .logo__img {
  display: inline-block;
  fill: #d22856;
}
@media (min-width: 1200px) {
  .logo--theme-color .logo__img {
    display: none;
  }
}

.logo__img-header {
  fill: #ffffff;
  margin-right: 11px;
  display: none;
}
@media (min-width: 1200px) {
  .logo__img-header {
    display: inline-block;
  }
}

.logo__img-footer {
  fill: #d22856;
  margin-right: 11px;
  display: none;
}
@media (min-width: 1200px) {
  .logo__img-footer {
    display: inline-block;
  }
}

.logo__text {
  fill: #ffffff;
}
@media (min-width: 1200px) {
  .logo__text {
    display: none;
  }
}

.logo--theme-color .logo__text {
  fill: #283645;
}

.logo__text-stars {
  display: none;
  fill: #ffffff;
}
@media (min-width: 1200px) {
  .logo__text-stars {
    display: inline-block;
  }
}

.logo--theme-color .logo__text-stars {
  fill: #283645;
}

.logo--theme-color:active .logo__text-stars,
.logo--theme-color:active .logo__text {
  fill: #283645;
  opacity: 0.3;
}

.logo--theme-color:active .logo__img,
.logo--theme-color:active .logo__img-footer {
  fill: #d22856;
  opacity: 0.3;
}

.main-menu {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  display: none;
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  text-align: center;
}
@media (min-width: 1200px) {
  .main-menu {
    position: static;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: auto;
    max-width: 690px;
    margin-right: -38px;
    margin-left: -38px;
  }
}

@media (min-width: 1200px) {
  .main-menu__item {
    margin: 0 38px;
  }
}

.main-menu__link {
  display: block;
  padding: 25px;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  box-sizing: border-box;
}
@media (min-width: 1200px) {
  .main-menu__link {
    padding: 19px 0;
    font-weight: 400;
    line-height: 18px;
    text-transform: none;
  }
}
.main-menu__link:hover {
  color: #d22856;
}
.main-menu__link:active {
  color: rgba(210, 40, 86, 0.3);
}
@media (min-width: 1200px) {
  .main-menu__link:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
.main-menu__link--current {
  position: relative;
}
.main-menu__link--current:hover {
  color: #ffffff;
}
.main-menu__link--current:active {
  color: #ffffff;
}

@media (min-width: 1200px) {
  .main-menu__link--current::after {
    position: absolute;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 2px;
    content: "";
    background-color: #ffffff;
  }
}

.button-open {
  font-family: inherit;
  appearance: none;
  cursor: pointer;
  align-self: end;
  background-color: transparent;
  border: none;
}
@media (min-width: 1200px) {
  .button-open {
    display: none;
  }
}
.button-open:hover .button-open__image {
  fill: #d22856;
}
.button-open:active .button-open__image {
  fill: rgba(210, 40, 86, 0.3);
}

.button-open__image {
  fill: #ffffff;
}

.button-close {
  font-family: inherit;
  appearance: none;
  cursor: pointer;
  display: none;
  background-color: transparent;
  border: none;
}
@media (min-width: 1200px) {
  .button-close {
    display: none;
  }
}
.button-close:hover .button-close__image {
  fill: #d22856;
}
.button-close:active .button-close__image {
  fill: rgba(210, 40, 86, 0.3);
}

.button-close__image {
  fill: #ffffff;
}

.index-title {
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  color: #283645;
  text-align: center;
}
@media (min-width: 660px) {
  .index-title {
    font-size: 40px;
    line-height: 48px;
  }
}
@media (min-width: 1200px) {
  .index-title {
    font-size: 50px;
    line-height: 68px;
  }
}
.index-title--theme-light {
  color: #ffffff;
}
@media (min-width: 660px) {
  .index-title--size-large {
    line-height: 58px;
  }
}
@media (min-width: 1200px) {
  .index-title--size-large {
    line-height: 68px;
  }
}

.slider-pagination {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 12px);
  column-gap: 19px;
  justify-content: center;
}

.slider-pagination__button {
  font-family: inherit;
  appearance: none;
  cursor: pointer;
  display: block;
  width: 12px;
  height: 12px;
  border-width: 3px;
  border-style: solid;
  border-color: transparent;
  padding: 0;
  border-radius: 50%;
  background-color: rgba(40, 54, 69, 0.3);
  background-clip: content-box;
  box-sizing: border-box;
}
.slider-pagination__button:hover {
  background-color: rgba(40, 54, 69, 0.6);
}
.slider-pagination__button:active {
  background-color: rgba(40, 54, 69, 0.1);
}
.slider-pagination__button--current {
  border-color: #283645;
  background-color: transparent;
}

.promo {
  background-color: #1d2631;
}
@media (min-width: 660px) {
  .promo {
    background-image: url("../img/promo-tablet.png"), url("../img/bg-promo-tablet.jpg");
    background-repeat: no-repeat, no-repeat;
    background-position: center bottom, center;
    background-size: auto, cover;
  }
}
@media (min-width: 660px) and (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/promo-tablet@2x.png"), url("../img/bg-promo-tablet@2x.jpg");
    background-size: 660px auto, cover;
  }
}
@media (min-width: 1200px) {
  .promo {
    background-image: url("../img/promo-desktop.png"), url("../img/bg-promo-desktop.jpg");
    background-position: right bottom, center;
    background-size: auto, cover;
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/promo-desktop@2x.png"), url("../img/bg-promo-desktop@2x.jpg");
    background-size: 1050px auto, cover;
  }
}

@media (min-width: 660px) {
  .promo__container {
    width: 620px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .promo__container {
    width: 940px;
  }
}

@media (min-width: 660px) {
  .promo__content {
    padding: 212px 320px 118px 0;
  }
}
@media (min-width: 1200px) {
  .promo__content {
    padding: 224px 640px 130px 0;
  }
}

.promo__slogan-wrap {
  padding-top: 95px;
  padding-bottom: 320px;
  background-image: url("../img/promo.png"), url("../img/bg-promo.jpg");
  background-repeat: no-repeat, no-repeat;
  background-position: 50% bottom, 0 0;
  background-size: auto, cover;
}
@media (min-resolution: 2dppx) {
  .promo__slogan-wrap {
    background-image: url("../img/promo@2x.png"), url("../img/bg-promo@2x.jpg");
    background-size: 320px auto, cover;
  }
}
@media (min-width: 660px) {
  .promo__slogan-wrap {
    padding-top: 0;
    padding-bottom: 0;
    background-image: none;
  }
}

.promo__slogan {
  width: 280px;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}
@media (min-width: 660px) {
  .promo__slogan {
    margin-bottom: 39px;
    background-image: none;
  }
}
@media (min-width: 1200px) {
  .promo__slogan {
    margin-bottom: 51px;
  }
}

.promo__download {
  width: 280px;
  padding: 28px 0 38px;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .promo__download {
    width: auto;
    padding: 0;
    margin: 0;
  }
}

.markets {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 47px);
  width: 204px;
  column-gap: 31.5px;
  row-gap: 10px;
  box-sizing: border-box;
}

.markets__item {
  width: 47px;
  height: 56px;
  box-sizing: border-box;
}

.markets__link {
  display: block;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
}
.markets__link--theme-android {
  background-image: url("../img/android-logo.svg");
}
.markets__link--theme-apple {
  background-image: url("../img/apple-logo.svg");
}
.markets__link--theme-microsoft {
  background-image: url("../img/microsoft-logo.svg");
}

.download {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.download__link {
  margin-bottom: 37px;
}
@media (min-width: 660px) {
  .download__link {
    margin-bottom: 46px;
  }
}
@media (min-width: 1200px) {
  .download__link {
    margin-bottom: 67px;
  }
}

.download__list {
  margin-bottom: 28px;
}
@media (min-width: 660px) {
  .download__list {
    margin-bottom: 38px;
  }
}
@media (min-width: 1200px) {
  .download__list {
    margin-bottom: 58px;
  }
}

.download__description {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 300;
  text-align: center;
  color: #ffffff;
  line-height: 24px;
}
@media (min-width: 660px) {
  .download__description {
    font-size: 18px;
  }
}

.advantages {
  text-align: center;
  background-color: #f2f2f2;
  background-image: url("../img/bg-advantages.svg");
  background-repeat: no-repeat;
  background-position: center top;
  background-size: 100% 250px;
}
@media (min-width: 660px) {
  .advantages {
    background-size: 100% 290px;
  }
}
@media (min-width: 1200px) {
  .advantages {
    background-size: 100% 415px;
  }
}

.advantages__wrap {
  padding-top: 46px;
  padding-bottom: 325px;
  background-image: url("../img/advantages.png");
  background-repeat: no-repeat;
  background-position: center bottom;
}
@media (min-resolution: 2dppx) {
  .advantages__wrap {
    background-image: url("../img/advantages@2x.png");
    background-size: 157px auto;
  }
}
@media (min-width: 660px) {
  .advantages__wrap {
    padding-top: 58px;
    padding-bottom: 49px;
    background-image: url("../img/advantages-tablet.png");
    background-position: left bottom;
  }
}
@media (min-width: 660px) and (min-resolution: 2dppx) {
  .advantages__wrap {
    background-image: url("../img/advantages-tablet@2x.png");
    background-size: 268px auto;
  }
}
@media (min-width: 1200px) {
  .advantages__wrap {
    padding-top: 77px;
    padding-bottom: 59px;
    background-image: none;
  }
}

.advantages__slogan {
  margin-top: 0;
  margin-bottom: 30px;
}
@media (min-width: 660px) {
  .advantages__slogan {
    margin-bottom: 5px;
  }
}
.advantages__slogan--second {
  margin-bottom: 123px;
}
@media (min-width: 1200px) {
  .advantages__slogan--second {
    margin-bottom: 72px;
  }
}

.advantages__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 43px;
  box-sizing: border-box;
}
@media (min-width: 660px) {
  .advantages__list {
    width: 310px;
    margin-left: auto;
    text-align: left;
    row-gap: 46px;
  }
}
@media (min-width: 1200px) {
  .advantages__list {
    width: 100%;
    grid-template-columns: repeat(3, 300px);
    text-align: center;
    column-gap: 20px;
    row-gap: 20px;
  }
}

.advantages__image-wrap {
  display: none;
}
@media (min-width: 1200px) {
  .advantages__image-wrap {
    display: block;
    height: 301px;
    margin-bottom: 59px;
    overflow: hidden;
  }
}
.advantages__image-wrap::after {
  content: "";
  display: block;
  width: 100%;
  height: 17px;
  background-image: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, #000000 49.8%, rgba(0, 0, 0, 0) 93.23%);
  filter: blur(4px);
  opacity: 0.3;
  margin-top: -8.5px;
}

@media (min-width: 1200px) {
  .advantages__image {
    margin-left: auto;
    margin-right: auto;
  }
}

.advantages__lead {
  margin-top: 0;
  margin-bottom: 21px;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  color: #000000;
}
@media (min-width: 660px) {
  .advantages__lead {
    margin-bottom: 24px;
  }
}
@media (min-width: 1200px) {
  .advantages__lead {
    width: 250px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 24px;
    font-size: 30px;
    line-height: 30px;
  }
}

.advantages__description {
  margin-top: 0;
  margin-bottom: 0;
}
@media (min-width: 1200px) {
  .advantages__description {
    width: 250px;
    margin-left: auto;
    margin-right: auto;
    font-size: 18px;
    line-height: 30px;
  }
}

.effects {
  display: none;
}
@media (min-width: 660px) {
  .effects {
    display: block;
  }
}
@media (min-width: 1200px) {
  .effects {
    background-color: #f2f2f2;
  }
}

.effects__content {
  padding: 102px 0 30px 310px;
  background-image: url("../img/effects.svg");
  background-repeat: no-repeat;
  background-position: left bottom;
}
@media (min-width: 1200px) {
  .effects__content {
    padding: 115px 35px 120px 370px;
    background-position: left top 17px;
    background-size: 298px auto;
  }
}

.effects__title {
  width: 170px;
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: 700;
  font-size: 30px;
  line-height: 36px;
  color: #000000;
}
@media (min-width: 1200px) {
  .effects__title {
    width: 100%;
    margin-bottom: 37px;
    line-height: 30px;
  }
