.effects__desc {
  margin-top: 0;
  margin-bottom: 24px;
}
@media (min-width: 1200px) {
  .effects__desc {
    width: 533px;
    margin-bottom: 47px;
    font-size: 18px;
    line-height: 30px;
  }
}

.effects__link {
  color: #d22856;
}
@media (min-width: 1200px) {
  .effects__link {
    font-size: 18px;
    line-height: 30px;
  }
}
.effects__link:hover {
  color: #283645;
}
.effects__link:active {
  color: rgba(40, 54, 69, 0.3);
}

.slider-button {
  font-family: inherit;
  appearance: none;
  cursor: pointer;
  background-color: transparent;
  border: none;
  padding: 0;
  font-size: 0;
}
.slider-button:hover .slider-button__img {
  fill: rgba(40, 54, 69, 0.6);
}
.slider-button:active .slider-button__img {
  fill: rgba(40, 54, 69, 0.1);
}

.slider-button__img {
  fill: rgba(40, 54, 69, 0.3);
}

.comments {
  padding: 50px 0 40px;
  text-align: center;
}
@media (min-width: 660px) {
