}
.control__mark-checkbox::before {
  display: none;
  width: 22px;
  height: 16px;
  margin: auto;
  content: "";
  background-image: url("../img/icons/form-tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.control__input:checked + .control__mark-checkbox::before {
  display: block;
}

.control__mark-radio {
  display: flex;
  width: 37px;
  height: 37px;
  margin-right: 22px;
  background-color: #ffffff;
  border: 2px solid #e5e5e5;
  border-radius: 50%;
}
.control__mark-radio::before {
  display: none;
  width: 15px;
  height: 15px;
  margin: auto;
  content: "";
  background-color: #d22856;
  border-radius: 50%;
}

.control__input:checked + .control__mark-radio::before {
  display: block;
}

