.styled-slider {
  height: 2px;
  appearance: none;
}

/*webkit*/
.styled-slider::-webkit-slider-thumb {
  appearance: none;
  width: 14px;
  height: 14px;
  border-radius: 7px;
  background: #ffffff;
  border: 2px solid #d22856;
  margin-top: -6px;
}

.styled-slider::-webkit-slider-runnable-track {
  height: 2px;
  background: #283645;
}

.styled-slider::-webkit-slider-thumb:active {
  background: #d22856;
}

/*mozilla*/
.styled-slider::-moz-range-thumb {
  width: 10px;
  height: 10px;
  border-radius: 7px;
  background: #ffffff;
  border: 2px solid #d22856;
  box-shadow: none;
}

.styled-slider::-moz-range-track {
  height: 2px;
  border: none;
  border-radius: 0;
  background: #283645;
  box-shadow: none;
}

.styled-slider::-moz-range-thumb:active {
  background: #d22856;
}

/*ms*/
.styled-slider::-ms-fill-upper {
  background: transparent;
  border-color: transparent;
}

.styled-slider::-ms-fill-lower {
  background: transparent;
  border-color: transparent;
}

.styled-slider::-ms-thumb {
  width: 14px;
  height: 14px;
  border-radius: 7px;
  background: #ffffff;
  border: 2px solid #d22856;
  box-shadow: none;
  margin-top: 0;
  box-sizing: border-box;
}

.styled-slider::-ms-track {
  height: 2px;
  border-radius: 0;
  background: #283645;
  border: none;
  box-shadow: none;
  box-sizing: border-box;
}

.styled-slider::-ms-thumb:active {
  background: #d22856;
}